import React, {useEffect} from 'react';
import {Button, Card, Col, Row} from 'react-bootstrap';
import {PricingPlan} from '../../../core/pricing';
import {useDispatch, useSelector} from 'react-redux';
import {RootState} from '../../../store';
import actions from '../../../store/actions';
import ReactGA from 'react-ga';
import './PricingScreen.css';
import {useHistory} from 'react-router';
import Media from 'react-media';
import {FeatureCard} from '../../../containers/Pricing/FeatureCard';
import {Profile} from "../../../core/profile";

interface PricingCardProps {
  plan: PricingPlan;
  onSelected: (id: string) => void;
}

const PricingCard: React.FC<PricingCardProps> = ({plan, onSelected}) => {
  return (
    <Card className={'price-block ' + (plan.bestValue ? 'popular' : '')}>
      <span className={'best-value'}>
        {plan.bestValue ? 'BEST VALUE' : ' '}
      </span>
      <h3>{plan.title}</h3>
      <br />
      <span className={'price'}>{plan.price ? `$${plan.price}` : 'FREE'}</span>
      <span> user / month</span>
      <Button onClick={() => onSelected(plan.title)}>START TRIAL</Button>
      <hr />
    </Card>
  );
};

export const PricingScreen: React.FC = () => {
  const plans: PricingPlan[] = [
    {
      plan: 'basic_plan',
      title: 'Basic',
      bestValue: false,
      price: 0,
    },
    {
      plan: 'premium_plan',
      title: 'Premium',
      bestValue: true,
      price: 8.99,
    },
    {
      plan: 'teams_plan',
      title: 'Teams',
      bestValue: false,
      price: 14.99,
    },
  ];

  const history = useHistory();
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(actions.features.getList());
  }, [dispatch]);

  useEffect(() => {
    console.log('Send event fill profile');
    ReactGA.event({
      category: 'User',
      action: 'Fill profile',
    });
    history.replace('/pricing');
  }, []);

  let profile: Profile = useSelector((state: RootState) => state.profile);
  const {data, status} = useSelector(
    (state: RootState) => state.features2.List,
  );

  const onPlanSelected = (plan: string) => {
    profile.plan = plan;
    dispatch(actions.profile.updateProfile(profile));
  };

  const sortByPrice = (a: PricingPlan, b: PricingPlan): number => {
    return a.price - b.price;
  };

  const sortByValue = (a: PricingPlan, b: PricingPlan): number => {
    if (a.bestValue) return -1;
    if (b.bestValue) return 1;
    return 0;
  };

  return (
    <div className="onescreen pricing-screen">
      <Row>
        <Col>
          <h1>Select plan you'd like to try</h1>
          <h2>Try all features free for 14 days</h2>
          <br />
        </Col>
      </Row>

      <Row className={'pricing'}>
        <Media queries={{small: '(max-width: 768px)'}}>
          {screenSizeIs =>
            screenSizeIs.small
              ? plans.sort(sortByValue).map(p => (
                  <Col lg="4" md="4" xs="12">
                    <FeatureCard
                      data={data}
                      type="small"
                      plan={p}
                      onSelected={onPlanSelected}
                    />
                  </Col>
                ))
              : plans.sort(sortByPrice).map(p => (
                  <Col lg="4" md="4" xs="12">
                    <FeatureCard
                      data={data}
                      type="wide"
                      plan={p}
                      onSelected={onPlanSelected}
                    />
                  </Col>
                ))
          }
        </Media>
      </Row>
    </div>
  );
};
