import React from 'react';
import {Button, Card} from 'react-bootstrap';
import {Feature} from '../../core/features';
import {PricingPlan} from '../../core/pricing';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import actions from '../../store/actions';
import {useDispatch} from 'react-redux';

interface FeatureCardProps {
  type: 'small' | 'wide';
  data: Feature[];
  plan: PricingPlan;
  onSelected: (id: string) => void;
}

export const FeatureCard: React.FC<FeatureCardProps> = ({
  type,
  data,
  plan,
  onSelected,
}) => {
  const dispatch = useDispatch();

  const onClick = (property: string) => {
    dispatch(actions.events.sendEvent(property));
  };

  const featuresRendered = data.map((f, i) => {
    const backgroundColor = i % 2 && !plan.bestValue ? 'highlight ' : '';

    if (f.is_header) {
      return <h3 className={'feature-point ' + backgroundColor}> {f.name}</h3>;
    }

    if (type === 'small' && f[plan.plan] === '') return <></>;

    const color = f[plan.plan] === '' ? ' hide' : '';

    const featureName = f[plan.plan] === '+' ? f.name : f[plan.plan];

    const icon =
      f[plan.plan] !== '' ? (
        <FontAwesomeIcon
          icon={'question-circle'}
          style={{marginLeft: '5px', marginRight: '8px'}}
          color={'rgba(0,110,255,0.51)'}
          onClick={() => onClick(f.name)}
        />
      ) : (
        <></>
      );

    return (
      <span className={'feature-point ' + backgroundColor + color}>
        {featureName}
        {icon}
      </span>
    );
  });

  return (
    <Card className={'price-block ' + (plan.bestValue ? 'popular' : '')}>
      <span className={'best-value'}>
        {plan.bestValue ? 'BEST VALUE' : ' '}
      </span>
      <h3>{plan.title}</h3>
      <br />
      <span className={'price'}>{plan.price ? `$${plan.price}` : 'FREE'}</span>
      <span> user / month</span>
      <Button onClick={() => onSelected(plan.title)}>START TRIAL</Button>
      <hr />

      {featuresRendered}
      <Button onClick={() => onSelected(plan.title)}>START TRIAL</Button>
    </Card>
  );
};
